.primeiro-botao{
    right:80px;
    transform:rotate(180deg);}
.botao-fechar{
  background: linear-gradient(115.04deg , #FF0000  -16.85% , rgba(255,0,0,0.6) 103.44%, #FF0000  )!important;  }
.botao-fechar>img{
  height:100% !important;}
