#menu-lateral-paginas>li>a:hover button{
  color:white !important;
  background-image: none !important;
  
}

#pyramid{
  width: 80%;
  height: 100%;
  top:0%;
  margin: 0 auto;
  position: absolute;
  perspective: 420px;
  perspective-origin: 50% 40%;
  /*animation: show 5s linear forwards;
  animation-delay: 5s;
  visibility: hidden;
  */
}

.side {
  position: absolute;
  right: 22%;
  top: 35%;
  width: 0;
  height: 0;
  border-left: 120px solid transparent;
  border-right: 120px solid transparent;
  border-bottom: 240px solid rgba(50,50,50,.3);
  transform-origin: 50% 0%;
  animation: spin 5s infinite linear;
  visibility: hidden;
  
  

}

.back {
  animation-delay:  2.5s;
 
}
  
.right {
  animation-delay: 1.25s;
 
}

.left {
  animation-delay: 3.75s;

}


@keyframes spin {
  0% {
    transform: rotateY(0deg) rotateX(30deg);
    visibility: visible;
    border-bottom-color: lighten(rgba(50,50,50,.6), 5%);
    
  }
  25% {
    transform: rotateY(90deg) rotateX(30deg);
    border-bottom-color: darken(rgba(50,50,50,.6), 5%);
    opacity: 1;
  }
  25.1% {
    opacity: 1;
  }
  50% {
    transform: rotateY(180deg) rotateX(30deg);
    border-bottom-color: darken(rgba(50,50,50,.6), 12%);
  }
  74.9% {
    opacity: 0;
  }
  75% {
    transform: rotateY(270deg) rotateX(30deg);
    border-bottom-color: darken(rgba(50,50,50,.6), 15%);
    opacity: 1;
  }
  100% {
    transform: rotateY(360deg) rotateX(30deg);
    border-bottom-color: lighten(rgba(50,50,50,.6), 5%);

  }
}

@keyframes show {
  0% {
   visibility: hidden;
    
  }
 
  100% {
   visibility: visible;
  }
}


