  .digitacao{
    animation: 
    typing 1.5s steps(40, end) ,
    blink-caret .40s step-end infinite;
   
  }
  /* Text appears*/
  @keyframes typing {
    from { width: 0 }
    to { width: 100%;
        
  }
  90%{
    border-right:.15em solid white;
  }
  95%{
    border:none;
  }
  100%{
    border:none;
  }
}
  /* The typewriter cursor effect */
  @keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: white }
    
  }




  .progress{
    height: 40px;
    background: black;
    border-radius: 100px;
    border-color: black; 
    box-shadow: none;
    margin-top: 15px;
    position: relative;
}
.progress .progress-title{
    width: 20%;
    height: 100%;
    background: black;
    border-color:black;
    float: left;
    margin: 0;
    font-size: 15px;
    color: white;
    line-height: 40px;
    letter-spacing: 1px;
    text-align: center;
    text-transform: uppercase;
    position: relative;
}
.progress .progress-title:after{
    content: "";
    border-color:black;
    position: absolute;
    top: 10px;
    right: -10px;
    z-index: 1;
}
.progress .progress-bar{
    width: 80%;
    background: black;
    border-color:black;
    position: relative;
}
.progress .progress-value{
    height: 100%;
    border-radius: 0 15px 15px 0;
    border-color:black;
    font-size: 15px;
    font-weight: bold;
    line-height: 40px;
    color: #fff;
    animation: animate-positive 2s;
}
.progress.red .progress-value{ background: #ec4251; }
.progress.green .progress-value{ background: #73cd21; }
.progress.blue .progress-value{ background: #2e9ce0; }
.progress.yellow .progress-value{ background: #eea921; }
@keyframes animate-positive{
    0%{ width: 0; }
}
@media only screen and (max-width: 480px){
    .progress .progress-title{
        font-size: 12px;
        letter-spacing: 0;
    }
}
